import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../views/home/index.vue'
import about from '../views/about/index.vue'
import intro from '../views/intro/index.vue'
import huigou from '../views/huigou/index.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: "/home",
   
  },
  {
    path: '/home',
    name: 'home',
    component: home,
    meta: {
      tabPage: true
    }
  },
  {
    path: '/about',
    name: 'about',
    component: about,
    meta: {
      tabPage: true
    }
  },
  {
    path: '/intro',
    name: 'intro',
    component: intro,
    meta: {
      tabPage: true
    }
  },
  {
    path: '/huigou',
    name: 'huigou',
    component: huigou,
    meta: {
      tabPage: true
    }
  },
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router



