<template>
  <div id="app">
    <div class="box-top-warp flex-col">
      <div class="box-top flex-row">
        <div class="box-top-logo flex-col"></div>
        <div class="box-top-right flex-col" @click="isSelectLan = !isSelectLan">
          <div class="box-top-right-center flex-row">
            <span class="box-top-right-text">{{ $t('commonText.header')}}</span>
            <img
              class="box-top-right-text-icon"
              referrerpolicy="no-referrer"
              src="./assets/img/button-icon.png"
            />
          </div>
        </div>
      </div>
    </div>
    <router-view />
    <div class="box-warp">
      <div class="box-mian">
        <div class="box-main-item">
          <div class="box-main-item-box" v-for="(item,index) in list" :key="index" @click="onClickPage(item, index)">
            <img v-if="defaultIndex == index" class="box-main-img" :src="item.active" alt="">
            <img v-else class="box-main-img" :src="item.img" alt="">
            <p class="box-main-name" :class="{'box-main-name-active': defaultIndex == index}">{{item.name}}</p>
          </div>
        </div>
      </div>
    </div>
    <div>
    <div v-if="isSelectLan" class="model-panel">
      <div class="model-mian">
        <p class="model-title">{{ $t('commonText.selectLanTitle')}}</p>
        <div class="model-mian-center">
          <div class="model-mian-center-item" :class="{'model-mian-center-item-active': lang == 'zh'}"  @click="selectLan('zh')">{{ $t('commonText.LanText1')}}</div>
          <div class="model-mian-center-item" :class="{'model-mian-center-item-active': lang == 'jp'}"  @click="selectLan('jp')">{{ $t('commonText.LanText2')}}</div>
          <div class="model-mian-center-item" :class="{'model-mian-center-item-active': lang == 'en'}"  @click="selectLan('en')">{{ $t('commonText.LanText3')}}</div>
        </div>
        <p @click="confirm" class="model-btn">
          {{ $t('commonText.selectLanBtn')}}
        </p>
      </div>
      <div class="model-close" @click="isSelectLan = !isSelectLan" ></div>
    </div>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    name: "App",
    data() {
      return {
        defaultIndex: 0,
        isSelectLan: false,
        lang: this.getStorage('localeLang') || 'zh',
      };
    },
    computed: {
      list (){
        return [{
            name: this.$t('commonText.tab1'),
            img: require('./assets/img/tab-01-def.png'),
            active: require('./assets/img/tab-01.png'),
            path: '/home',
          },
          {
            name: this.$t('commonText.tab2'),
            img: require('./assets/img/tab-02-def.png'),
            active: require('./assets/img/tab-02.png'),
            path: '/huigou',
          },
          {
            name: this.$t('commonText.tab3'),
            img: require('./assets/img/tab-03-def.png'),
            active: require('./assets/img/tab-03.png'),
            path: '/intro',
          },
          {
            name: this.$t('commonText.tab4'),
            img: require('./assets/img/tab-04-def.png'),
            active: require('./assets/img/tab-04.png'),
            path: '/about',
          }
        ]
      }
    },
    methods: {
      selectLan(lang){
        this.lang = lang
      },
      confirm() {
        this.isSelectLan = false
        this.$i18n.locale = this.lang
        this.setStorage('localeLang', this.$i18n.locale)
      },
      onClickPage(item, index) {
        this.defaultIndex = index
        window.scrollTo({
          top: 0
        })
        this.$router.push(item.path)
      },
    },
    mounted (){
      this.$i18n.locale = this.getStorage('localeLang')
    }
  };
</script>

<style lang="css" src="./assets/common.css"></style>
<style scoped>
  .model-panel{
    width: 100%;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 999;
  }
  .model-mian{
    width: 343px;
    height: 354px;
    background: #1D2341;
    margin: 100px 0 0 16px;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
  }
  .model-title{
    width: 100%;
    height: 40px;
    padding: 0 12px;
    font-size: 13px;
    background: #26325B;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-family: STHeitiTC-Medium, STHeitiTC;
    font-weight: 500;
    color: #FFFFFF;
  }
  .model-mian-center{
    width: 320px;
    overflow: hidden;
    margin: 24px 0;
  }
  .model-mian-center-item{
    width: 280px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    color: #FFFFFF;
    margin-left: 30px;
    margin-bottom: 12px;
    background: #1D2341;
    border-radius: 6px;
    border: 1px solid #26325B;
  } 
  .model-mian-center-item-active{
    background: #26325B;
  }
  .model-btn{
    width: 100px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(90deg, #53D6FB 0%, #AB6BE4 100%);
    border-radius: 20px;
    font-size: 14px;
    font-family: STHeitiTC-Light, STHeitiTC;
    font-weight: 300;
    color: #FFFFFF;
    position: absolute;
    bottom: 24px;
    left: 50%;
    margin-left: -50px;
  }
  .model-close{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: url(./assets/img/close-icon.png) no-repeat center;
    background-size: 100% 100%;
    margin: 32px auto;
  }
</style>
<style>
  #app{
    width: 100%;
    height: 100%;
    position: relative;
  }
  * {
    padding: 0;
    margin: 0;
  }

  .box-top-warp {
    z-index: 67;
    height: 52px;
    background-color: rgba(29, 35, 65, 1);
    width: 375px;
    justify-content: flex-end;
    padding-bottom: 9px;
    align-items: center;
  }

  .box-top {
    z-index: auto;
    width: 343px;
    height: 31px;
    display: flex;
    justify-content: space-between;
  }

  .box-top-logo {
    z-index: 168;
    width: 210px;
    height: 22px;
    background: url(./assets/img/logo.png) no-repeat center;
    background-size: 100% 100%;
    margin-top: 9px;
  }

  .box-top-right {
    z-index: 160;
    height: 28px;
    border-radius: 14px;
    background: linear-gradient(90deg, #53D6FB 0%, #AB6BE4 100%);
    /* margin-left: 107px; */
    padding: 0 6px;
    align-items: center;
    justify-content: center;
  }

  .box-top-right-center {
    z-index: auto;
    height: 14px;
    justify-content: space-between;
    align-items: center;
    padding: 0 8px 0 4px;
    box-sizing: border-box;
  }

  .box-top-right-text {
    z-index: 162;
    /* width: 43px; */
    height: 14px;
    margin-right: 4px;
    display: block;
    overflow-wrap: break-word;
    color: rgba(255, 255, 255, 1);
    font-size: 12px;
    font-family: Helvetica-Light;
    white-space: nowrap;
    line-height: 14px;
    text-align: left;
    font-family: Helvetica-Light, Helvetica;
  }

  .box-top-right-text-icon {
    z-index: 163;
    width: 10px;
    height: 6px;
  }

  .box-warp {
    z-index: 155;
    height: 56px;
    background-color: #1D2341;
    width: 100%;
    justify-content: flex-start;
    position: fixed;
    left: 0;
    bottom: 0;
  }

  .box-mian {
    z-index: auto;
    width: 100%;
    height: 100%;
    justify-content: center;
  }

  .box-main-item {
    z-index: auto;
    width: 309px;
    justify-content: space-between;
    margin: 8px 0 0 33px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .box-main-item-box{
    text-align: center;
  }

  .box-main-img {
    z-index: 161;
    width: 20px;
    height: 20px;
  }

  .box-main-name {
    width: 28px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow-wrap: break-word;
    font-size: 12px;
    color: #CCCCCC;
    white-space: nowrap;
    text-align: center;
  }

  .box-main-name-active {
    color: #2ECEFB;
  }
</style>



