<template>
  <div class="page flex-col">
    <div class="outer1 flex-col">
      <div class="outer3 flex-col">
        <div class="box4 flex-col">
          <div class="section1 flex-col">
            <div class="box5 flex-col">
              <div class="layer3 flex-col">
              </div>
              <span class="txt2">RAINbow</span>
              <div class="layer4 flex-row">
                <span class="info3">{{ $t('home.mub1')}}</span>
                <span class="word4">{{ $t('home.mubCon')}}</span>
              </div>
              <div class="layer5 flex-row">
                <span class="info4">{{ $t('home.jindu')}}</span>
                <span class="info5">0%</span>
              </div>
              <div class="layer6 flex-col"><div class="layer7 flex-col"></div></div>
              <div class="layer8 flex-row">
                <span class="txt3">0%</span>
                <span class="txt4">100%</span>
              </div>
              <div class="layer9 flex-row">
                <div class="box6 flex-col" v-for="(item, index) in loopData0" :key="index">
                  <div class="bd1 flex-col">
                    <span class="info6" :style="{ color: item.lanhufontColor0 }" v-html="item.lanhutext0"></span>
                    <span class="word5" :style="{ color: item.lanhufontColor1 }" v-html="item.lanhutext1"></span>
                  </div>
                </div>
              </div>
              <span class="word6">
                {{ $t('home.mub2')}}
              </span>
              <span class="word7">{{ $t('home.timeTitle')}}</span>
              <div class="layer10 flex-row">
                <div class="outer4 flex-col" v-for="(item, index) in loopData1" :key="index">
                  <div class="bd2 flex-col">
                    <div class="mod2 flex-col"><span class="info7" v-html="item.lanhutext0"></span></div>
                    <span class="word8" v-html="item.lanhutext1"></span>
                  </div>
                </div>
              </div>
            </div>
            <!-- <div class="box7 flex-col"><span class="word9">{{ $t('home.tip')}}</span></div> -->
          </div>
          <div class="section3 flex-col">
            <div class="main3 flex-col">
              <div class="section4 flex-col"><span class="txt5">{{ $t('home.chengyuan')}}</span></div>
              <div class="section5 flex-row">
                <div class="block2 flex-col">
                  <img
                    class="pic1"
                    referrerpolicy="no-referrer"
                    src="./assets/img/pic.png"
                  />
                </div>
                <span class="txt6">{{ $t('home.chengyuanText')}}
                </span>
              </div>
            </div>
          </div>
          <div class="section6 flex-col">
            <div class="layer11 flex-col" v-for="(item, index) in loopData2" :key="index">
              <div class="bd3 flex-col">
                <div class="layer12 flex-col"><span class="txt7" v-html="item.lanhutext0"></span></div>
                  <span v-if="item.slot1 === 1" class="infoBox2">{{ $t('home.contryCon')}}
                    <br />{{ $t('home.contryCon1')}}
                  </span>
                <span v-if="item.slot2 === 2" class="infoBox1">
                  {{ $t('home.fengxian1')}}                  <br />
                  {{ $t('home.fengxian2')}}                     <br />
                  {{ $t('home.fengxian3')}}                     <br />
                  {{ $t('home.fengxian4')}}                   </span>
            
              </div>
            </div>
          </div>
          <div class="section7 flex-col">
            <div class="main4 flex-col">
              <div class="group2 flex-col"><span class="word10">RAINbow</span></div>
              <span class="txt8">
                {{ $t('home.rainbow1')}}              
                </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
    };
  },
  computed:{
    loopData0(){
      return [
        {
          lanhutext0: this.$t('home.startTime'),
          lanhufontColor0: 'rgba(147,174,232,1)',
          lanhutext1:this.$t('home.endTime'),
          lanhufontColor1: 'rgba(147,174,232,1)'
        },
        {
          lanhutext0: '2021-09-2318:00:00',
          lanhufontColor0: 'rgba(255,255,255,1)',
          lanhutext1: '2021-09-24&nbsp;10:00:00',
          lanhufontColor1: 'rgba(255,255,255,1)'
        }
      ]
    },
    loopData1(){
      return [
        { lanhutext0: '00', lanhutext1: this.$t('home.time1') },
        { lanhutext0: '00', lanhutext1: this.$t('home.time2') },
        { lanhutext0: '00', lanhutext1: this.$t('home.time3') },
        { lanhutext0: '00', lanhutext1: this.$t('home.time4') },
        { lanhutext0: '00', lanhutext1: this.$t('home.time5') }
      ]
    },
    loopData2(){
      return [
        { lanhutext0: this.$t('home.contryTitle'), slot1: 1 },
        { lanhutext0: this.$t('home.fengxian'), slot2: 2 }
      ]
    }
  },
  methods: {}
};
</script>
<style scoped lang="css" src="./assets/index.css" />


